<template>
  <div id="app">
    <label class="tt">: </label>
    <j-date-picker v-model="value1"
                   :width="width1"
                   :placeholder="placeholder"
                   :picker-options="pickerOptions"
                   :rangeSeparator="rangeSeparator"
                   @change="onDateChange"
                   :disabled="disabled"
                   :showLunarClass="showLunarClass"
                   :showLunarControl="showLunarControl"
                   :type="type"
                   :showBackYears="showBackYears"
                   :showLunarIcon="showLunarIcon"
                   :format="format"></j-date-picker>
    <p class="tt" style="padding-left:10px;">{{ value1 }}</p>
    <hr>
    <p>2017-01-01L2017-01-01LR2017-06-01; ()['2017-01-02','2017.02.01'];()['L2017-01-02','L2017.02.01'];</p>
    <hr>

    <input type="button" @click="onBtnClick" value="" />

    <div class="param">
      <h4></h4>
      <label></label><input type="text" v-model="width1"  /><br>

      <label></label><input type="text" v-model="placeholder"  /><br>

      <label></label><input type="text" v-model="rangeSeparator"  /><br>

      <label></label>
      <select v-model="type">
        <option value="DATE">DATE</option>
        <option value="DATERANGE">DATERANGE</option>
      </select>
      <br>

      <label></label>
      <select v-model="showLunarClass">
        <option value="FULLLUNAR">FULLLUNAR</option>
        <option value="LUNAR">LUNAR</option>
        <option value="NUMBER">NUMBER</option>
        <option value="MIX">MIX</option>
      </select>
      <br>

      <label></label><input type="checkbox" v-model="showLunarControl"  /><br>

      <label></label><input type="checkbox" v-model="showLunarIcon"  /><br>

      <label></label><input type="checkbox" v-model="disabled"  /><br>

      <label></label><input type="checkbox" v-model="editable"  /><br>

      <label></label><input type="checkbox" v-model="clearable"  /><br>

      <label></label><input type="number" v-model="showBackYears"  /><br>

      <label></label><input type="text" v-model="format"  /> ()<br>

    </div>
    <div class="api">
      <h3></h3>
      <p></p>
      <p>Script:</p>
      <p class="js">
        import JDatePicker from '@/components/JDatePicker.vue'
        <br>
        export default{<br>
          <i style="padding-left:10px;"></i>components:{ JDatePicker },<br>
            <i style="padding-left:20px;"></i>data:{<br>
              <i style="padding-left:30px;"></i>return{<br>
                <i style="padding-left:40px;"></i>pickerOptions: {<br>
                  <i style="padding-left:50px;"></i>disabledDate (time) {<br>
                    <i style="padding-left:60px;"></i>return time.getTime() < Date.now() - 8.64e7;<br>
                  <i style="padding-left:50px;"></i>}<br>
                <i style="padding-left:40px;"></i>}<br>
              <i style="padding-left:30px;"></i>}<br>
          <i style="padding-left:20px;"></i>}<br>
        }
      </p>
      <p>HTML:</p>
      <p class="js">
       < j-date-picker v-model="value1" width="300" show-lunar-class="fulllunar" :picker-options="pickerOptions" :show-back-years="5" :show-lunar-control='showLunarControl' show-lunar-icon format="yyyy.mm.dd">< /j-date-picker>
      </p>

      <h3></h3>
      <table border="1" cellspacing="0" width="100%">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <td>value</td>
          <td>(v-model);<br></td>
          <td>String,Date,Array</td>
          <td>-</td>
          <td>-</td>
        </tr>
        <tr>
          <td>type</td>
          <td></td>
          <td>String</td>
          <td>DATE()<br>DATETANGE()</td>
          <td>DATE</td>
        </tr>
        <tr>
          <td>editable</td>
          <td>;'NUMBER',false</td>
          <td>Boolean</td>
          <td>true<br>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>clearable</td>
          <td></td>
          <td>Boolean</td>
          <td>true<br>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>disabled</td>
          <td></td>
          <td>String</td>
          <td>true<br>false</td>
          <td>false</td>
        </tr>
        <tr>
          <td>width</td>
          <td></td>
          <td>String</td>
          <td>-</td>
          <td>200px/200</td>
        </tr>
        <tr>
          <td>placeholder</td>
          <td></td>
          <td>String</td>
          <td> - </td>
          <td> - </td>
        </tr>
        <tr>
          <td>show-lunar-class</td>
          <td></td>
          <td>String</td>
          <td>
            FULLLUNAR()<br>LUNAR()<br>NUMBER()<br>MIX()<br>
          </td>
          <td>NUMBER</td>
        </tr>
        <tr>
          <td>show-lunar-icon</td>
          <td></td>
          <td>Boolean</td>
          <td>true<br>false</td>
          <td>false</td>
        </tr>
        <tr>
          <td>show-back-years</td>
          <td>;<br>;<br></td>
          <td>Number</td>
          <td></td>
          <td>2</td>
        </tr>
        <tr>
          <td>format</td>
          <td></td>
          <td>String</td>
          <td>YYYY MM DD HH  MM SS</td>
          <td>YYYY-MM-DD</td>
        </tr>
        <tr>
          <td>show-lunar-control</td>
          <td></td>
          <td>Boolean</td>
          <td>true<br>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>range-separator</td>
          <td></td>
          <td>String</td>
          <td> - </td>
          <td> '-' </td>
        </tr>
        <tr>
          <td>picker-options</td>
          <td></td>
          <td>object</td>
          <td> - </td>
          <td> {} </td>
        </tr>
      </table>

      <h3>picker-options</h3>
      <table border="1" cellspacing="0" width="100%">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <td>disabledDate</td>
          <td> Boolean</td>
          <td>Function</td>
          <td>-</td>
          <td>-</td>
        </tr>
      </table>

      <h3></h3>
      <table border="1" cellspacing="0" width="100%">
        <tr>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <td>change</td>
          <td>input</td>
          <td></td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>

  import JDatePicker from '@/components/JDatePicker.vue'

  export default{
    name: 'app',
    components:{ JDatePicker },
    data(){
      return {
        value1:new Date(),
        type:'DATE',
        showLunarClass:'NUMBER',
        showBackYears:2,
        showLunarIcon:true,
        showLunarControl:true,
        width1:'600',
        format:'YYYY.MM.DD',
        placeholder:'',
        rangeSeparator:'-',
        disabled:false,
        editable:true,
        clearable:true,
        pickerOptions: {
//          disabledDate (time) {
//            return time.getTime() < Date.now() - 8.64e7;
//          }
        },
      }
    },
    methods:{
      onDateChange(e){
          console.log('change=>',e,'value:',this.value1)
      },
      onBtnClick(){
          alert(this.value1)
      }
    },
    created(){
//        this.value1 = [new Date(),new Date()];
    }
  }
</script>

<style>
  #app{
    margin:50px auto;
    padding-top:30px;
    width:70%;
  }
  #app .tt{ vertical-align: top; line-height: 34px; padding:0 5px;}
  .api{
    padding:20px 0;
    text-align: left;
    line-height: 24px;
    font-size: 14px;
    color:#555;
  }
  .param{
    line-height: 44px;
  }
  .js{
    padding:10px;
    border:1px solid #e8edf0;
    background-color: #ECF1F4;
    border-radius: 5px;
  }
  table{ text-align: center}
</style>



